'use client'
import { useForm } from "react-hook-form"
import { Button } from "@/components/ui/button"
import {
  Form,
  FormControl,
  FormDescription,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from "@/components/ui/form"
import { Input } from "@/components/ui/input"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"

const Donate = () => {
  const form = useForm()
  const onSubmit = async () => {
    // TODO : handle on submit
  }
  return (
    <div className="w-full h-full">
      <div className="w-full">
        <h1 className="text-center text-3xl font-bold mb-5">Pickup Details</h1>
      </div>
      <Form {...form}>
        <form onSubmit={form.handleSubmit(onSubmit)} className="px-5 flex flex-col gap-4 h-full">
          <FormField
            control={form.control}
            name="messname"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Mess name</FormLabel>
                <FormControl>
                  <Input placeholder="mess name" {...field} />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
          <FormField
            control={form.control}
            name="foodtype"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Food type</FormLabel>
                <Select onValueChange={field.onChange} defaultValue={field.value}>
                  <FormControl>
                    <SelectTrigger className="w-full">
                      <SelectValue placeholder="Veg / Non Veg" />
                    </SelectTrigger>
                  </FormControl>
                  <SelectContent>
                    <SelectItem value="Veg">Veg</SelectItem>
                    <SelectItem value="Non veg">Non Veg</SelectItem>
                  </SelectContent>
                </Select>
                <FormMessage />
              </FormItem>
            )}
          />
          <FormField
            control={form.control}
            name="category"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Category</FormLabel>
                <Select onValueChange={field.onChange} defaultValue={field.value}>
                  <FormControl>
                    <SelectTrigger className="w-full">
                      <SelectValue placeholder="Chapati/ snacks/ rice" />
                    </SelectTrigger>
                  </FormControl>
                  <SelectContent>
                    <SelectItem value="gravyveg">Gravy vegetable</SelectItem>
                    <SelectItem value="dryveg">Dry vegetable</SelectItem>
                    <SelectItem value="chapati">Chapati</SelectItem>
                    <SelectItem value="rice">Rice</SelectItem>
                    <SelectItem value="snacks">Snacks</SelectItem>
                  </SelectContent>
                </Select>
                <FormMessage />
              </FormItem>
            )}
          />
          <FormField
            control={form.control}
            name="usebefore"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Suggested use before</FormLabel>
                <FormControl>
                  <Input placeholder="Use before" {...field} />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
          <FormField
            control={form.control}
            name="info"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Additional info</FormLabel>
                <FormControl>
                  <Input placeholder="Additional info" {...field} />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
          <FormField
            control={form.control}
            name="photo"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Upload photo</FormLabel>
                <FormControl>
                  <Input type="file" draggable="true"/>
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
          <div className="flex justify-center ">
            <Button type="submit">Submit</Button>
          </div>
        </form>
      </Form>
    </div>
  )
}

export default Donate